import userService from '../../services/userService'
import router from '../../router'

const state = {
  csrfToken: null,
  loggedUser: null,
}

const getters = {
  getLoggedUser(state) {
    return state.loggedUser
  },
  getToken(state) {
    return state.csrfToken
  },
}

const mutations = {
  login(state, payload) {
    state.csrfToken = payload.csrfToken
    state.loggedUser = payload.user
  },
  logout(state) {
    state.csrfToken = null
    state.loggedUser = null
  },
}

const actions = {
  loadUsersCash({commit}) {
    var auth = JSON.parse(sessionStorage.getItem('auth'))
    if (auth && auth.user.role === 'client') {
      commit('login', auth)
    }
  },
  login({commit}, credentials) {
    return userService.login(credentials.email, credentials.password)
      .then(res => {
        if (res.user.role === 'client') {
          // Allow only clients to login
          sessionStorage.setItem('auth', JSON.stringify(res))
          commit('login', res)
        } else {
          // Do NOT allow non-admin users to login
          return Promise.reject(new Error('Do not have permission.'))
        }
      })
      .catch(errMsg => Promise.reject(errMsg))
  },
  logout({commit}, credentials) {
    commit('logout')
    router.push('/login')
    sessionStorage.clear()
    return userService.logout()
  },
}

export default {
  state,
  mutations,
  actions,
  getters,
}



