<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Cryptocurrency</th>
        <th>Ticker</th>
        <th>Share</th>
        <th>Value (BGN)</th>
        <th>Number of coins</th>
        <th>Price (BGN)</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="coin in coins">
        <td>{{ coin.name }}</td>
        <td>{{ coin.ticker }}</td>
        <td class="text-right">{{ coin.share }}%</td>
        <td class="text-right">{{ coin.valueBGN }}</td>
        <td class="text-right">{{ coin.pieces }}</td>
        <td class="text-right">{{ coin.priceBGN }}</td>
      </tr>
    </tbody>

  </table>
</template>


<script>
export default {
  props: ['coins'],
}
</script>



