<template>
  <div>

    <vue-chart
      chart-type="PieChart"
      :columns="columns"
      :rows="rows"
      :options="options">
    </vue-chart>

  </div>
</template>


<script>
export default {
  props: ['assets'],
  data() {
    return {
      columns: [
        {
          'type': 'string',
          'label': 'Coin',
        },
        {
          'type': 'number',
          'label': 'Value in BGN',
        },
      ],
      options: {
        width: 800,
        height: 500,
        is3D: true,
        legend: 'labeled',
        pieSliceText: 'none',
      },
    }
  },
  computed: {
    rows: function() {
      return this.assets.map(asset => [asset.name, asset.valueBGN])
    },
  },
}
</script>



